* {
  box-sizing: border-box;
  margin: 0;
  border: 0;
}

@font-face {
  font-family: "PressStart2P-Regular";
  src: url(./PressStart2P-Regular.ttf);
}

@media (prefers-reduced-motion: no-preference) {
  .us {
    animation: us infinite 20s linear;
  }
  .rainbow-container {
    animation: us reverse infinite 20s linear;
  }
  .robin-container {
    animation: us reverse infinite 20s linear;
  }
  .robin,
  .rainbow {
    animation: walk infinite 20s linear;
  }
  .oscar {
    animation: shoot infinite 5s linear;
  }
}

@keyframes walk {
  0% {
    transform: translate3d(0%, 0%, 0);
  }
  100% {
    transform: translate3d(-100%, 0%, 0);
  }
}

@keyframes shoot {
  0% {
    top: 0;
    left: 0;
  }
  100% {
    top: 100%;
    left: 100%;
  }
}

@keyframes us {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.App {
  display: flex;
  align-content: center;
  justify-content: center;
  flex-direction: column;
  letter-spacing: 0.1rem;
  height: 100%;
  width: 100%;
  line-height: 1.3rem;
}

.message {
  text-align: center;
  margin-top: 5vh;
  font-size: 2rem;
}

@media only screen and (max-width: 600px) {
  .message {
    background-color: lightskyblue;
    padding: 10px;
    margin: 5vh auto 0;
    font-size: 1rem;
  }
}

body {
  background-color: #000522;
  font-family: "PressStart2P-Regular", monospace;
  user-select: none;
  overflow: hidden;
  height: 100%;
  width: 100%;
  margin: 0;
  color: white;
}

.world-container {
  height: 95vw;
  width: 95vw;
  position: relative;
}

.earth {
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-position: right top;
  background-size: contain;
  background-image: url("./flatearth6.png");
}

.us {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  animation-timing-function: steps(512);
}

.rainbow-container {
  height: 10%;
  width: 10%;
  position: absolute;
  top: 48%;
  left: 1%;
  z-index: 2;
  overflow: hidden;
}
.rainbow {
  height: 100%;
  background-image: url(./rainbowa.png);
  width: 6400%;
  background-size: cover;
  animation-timing-function: steps(64);
  position: absolute;
}

.robin-container {
  height: 10%;
  width: 10%;
  position: absolute;
  top: 48%;
  left: 90%;
  z-index: 2;
  overflow: hidden;
  animation-timing-function: steps(64);
}
.robin {
  height: 100%;
  background-image: url(./robina.png);
  width: 6400%;
  background-size: cover;
  animation-timing-function: steps(64);
  position: absolute;
}

.oscar {
  height: 5vh;
  width: 5vh;
  background-image: url(./cat.png);
  background-size: cover;
  position: absolute;
  z-index: -1;
}
